<template>
  <div>
    <!-- 搜索页面头部 -->
    <div class="search-header">
      <!-- 后退按钮 -->
      <van-icon name="arrow-left" color="white" size="0.48rem" class="goback" @click="$router.back()" />
      <!-- 搜索组件 -->
      <van-search placeholder="请输入搜索关键词" background="#007BFF" shape="round" v-fofo v-model.trim="searchWord" @input="searchWordChangeFn" @search="searchInputFn" />
    </div>

    <!-- 搜索建议列表 -->
    <div class="sugg-list" v-if="searchWord.length !== 0">
      <div class="sugg-item" v-for="(item, index) in suggestList" :key="index" v-html="hightlightKeywordsFn(item, searchWord)" @click="searchSuggestFn(item)"></div>
    </div>

    <!-- 搜索历史记录 -->
    <div class="search-history" v-else>
      <!-- 标题 -->
      <van-cell title="搜索历史">
        <!-- 使用 right-icon 插槽来自定义右侧图标 -->
        <template #right-icon>
          <van-icon name="delete" class="search-icon" @click="clearHistoryFn" />
        </template>
      </van-cell>

      <!-- 历史列表 -->
      <div class="history-list">
        <span class="history-item" v-for="(item, index) in history" :key="index" @click="searchHistoryFn(item)">{{ item }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { getSuggestListAPI } from '@/api/api.js'
export default {
  name: 'Search',
  data() {
    return {
      // 搜索框中输入的文字
      searchWord: '',
      // 防抖的定时器
      timer: null,
      // 获取到的搜索建议列表
      suggestList: [],
      // 搜索历史数据
      history: JSON.parse(localStorage.getItem('his')) || []
    }
  },
  methods: {
    // 搜索输入框内容变化的事件
    searchWordChangeFn() {
      clearTimeout(this.timer)
      // 需求：点击搜索框的叉号，清空搜索建议列表
      if (this.searchWord.length === 0) {
        this.suggestList = []
      } else {
        // 发现我只要一变化，就会触发一次这个事件，所以需要防抖来控制事件的频繁发生
        // 这里的timer不能在函数内定义，不然没法一进函数就清除定时器
        this.timer = setTimeout(async () => {
          if (this.searchWord.length === 0) return
          // console.log(this.searchWord)
          const res = await getSuggestListAPI({
            keywords: this.searchWord
          })
          this.suggestList = res.data.data.options
        }, 500)
      }
    },
    // 搜索关键字高亮的方法
    // 思想：利用字符串的replace方法可以将字符串中的第一个实参替换成第二个实参，缺点：只能替换第一个，那就用replaceAll方法
    hightlightKeywordsFn(originStr, keywords) {
      if (originStr === null) return
      // 如果要使用变量作为正则的匹配条件，就不能使用语法糖/ /来简化正则的写法
      const reg = new RegExp(keywords, 'ig') // 全局替换(g)和忽略大小写(i)
      // return originStr.replaceAll(keywords, `<span style="color: red;">${keywords}</span>`)
      // 但这样做还有个问题，例如输入java，它无法匹配大小写，于是，我们就去查replace方法的文档
      return originStr.replace(reg, (match) => {
        // match代表的是匹配时，原字符串中的那个字符用的是字符串原来的字符
        return `<span style="color: red;">${match}</span>`
      })
    },

    // 现在需要通过三种方式跳转到搜索结果页
    // 1 点击手机键盘的搜索/回车跳转
    searchInputFn() {
      // 问题：不允许输入空格去查询
      // 解决：加一个判断条件
      if (this.searchWord.length > 0) {
        // 保存到history数组中
        this.history.unshift(this.searchWord)
        // 这里必须要让路由跳转最后执行,否则是监听不到监听器变化的，因为路由跳转是在监听器之前执行的，一旦路由跳转，组件就会被销毁
        setTimeout(() => {
          this.$router.push(`/search_result/${this.searchWord}`)
        }, 0)
      }
    },
    // 2 点击搜索建议项跳转
    searchSuggestFn(keywords) {
      this.history.unshift(keywords)
      setTimeout(() => {
        this.$router.push(`/search_result/${this.searchWord}`)
      }, 0)
    },
    // 3 点击搜索历史记录跳转
    searchHistoryFn(keywords) {
      this.$router.push(`/search_result/${keywords}`)
    },
    // 清空历史记录
    clearHistoryFn() {
      this.history = []
    }
  },
  // 把搜索记录本地存储起来
  // 通过一个侦听器来监听history的改变，并存储到本地当中
  watch: {
    history: {
      deep: true,
      handler() {
        // 问题：不允许history数组中有重复的元素
        // 解决：在ES6中新增了两种引用数据类型：Set  Map
        // Set：无序不重复的value集合体，没有下角标，所以传入的数组元素有重复的会自动清除掉重复元素，返回无重复的Set对象
        const theSet = new Set(this.history)
        // 再将重复的Set类型对象转回成为Array数组类型
        const arr = Array.from(theSet)
        // 立刻保存到本地，但是本地存储是无法存储数组的，只能存储字符串，所以需要转换
        localStorage.setItem('his', JSON.stringify(arr))
      }
    }
  }
}
</script>

<style lang="less" scoped>
.search-header {
  height: 46px;
  display: flex;
  align-items: center;
  background-color: #007bff;
  overflow: hidden;
  /*后退按钮*/
  .goback {
    padding-left: 14px;
  }
  /*搜索组件*/
  .van-search {
    flex: 1;
  }
}

/*搜索建议列表样式 */
.sugg-list {
  .sugg-item {
    padding: 0 15px;
    border-bottom: 1px solid #f8f8f8;
    font-size: 14px;
    line-height: 50px;
    // 实现省略号的三行代码
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

/**搜索历史 */
.search-icon {
  font-size: 16px;
  line-height: inherit;
}

.history-list {
  padding: 0 10px;
  .history-item {
    display: inline-block;
    font-size: 12px;
    padding: 8px 14px;
    background-color: #efefef;
    margin: 10px 8px 0px 8px;
    border-radius: 10px;
  }
}
</style>
